
.card{
  width: fit-content;
  height: fit-content;
  background-color: white;
  border-radius: 20px;
  border: 1px solid lightgrey;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
  display: flex;
  flex-direction: column;
  padding: 30px;
  justify-content: center;
  align-items: center;
}

.card input {
  width: 100%;
    font-size: 18px;
    padding: 10px;
    border-radius: 10px;
    border: 1px solid lightgrey;
    outline: none;
    margin: 20px;
}
.card input:focus{
  border: 1px solid rgb(129, 129, 129);
  outline: none;
}

.card button{
  width: fit-content;
  padding: 10px 30px;
  border: none;
  border-radius: 5px;
  background-color: rgb(5, 117, 5);
  color: rgb(255, 255, 255);
  font-weight: 900;
  margin-left: 10px;
}